import './App.css';
import { MemoryRouter as Router, Routes, Route } from 'react-router-dom';
import VetBookingWizard from './VetBookingWizard';
import BookingSuccess from './BookingSuccess';

// WordPress-compatible version that doesn't interfere with page routing
function WordPressApp() {
  return (
    <Router initialEntries={['/']} initialIndex={0}>
      <div className="App">
        <Routes>
          <Route path="/" element={<VetBookingWizard />} />
          <Route path="/booking-success" element={<BookingSuccess />} />
        </Routes>
      </div>
    </Router>
  );
}

export default WordPressApp;