// PaymentForm.js - Save this in frontend/src/PaymentForm.js
import React, { useState } from 'react';
import {
  CardElement,
  Elements,
  useStripe,
  useElements
} from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';
import { AlertCircle, CreditCard, Lock, CheckCircle } from 'lucide-react';
import { hybridBookingAPI as bookingAPI } from './config/api-hybrid';

// Initialize Stripe with publishable key from environment
let stripePromise = null;

// Function to initialize Stripe with key from backend
const initializeStripe = async () => {
  if (!stripePromise) {
    try {
      const data = await bookingAPI.getStripeConfig();

      // Respect backend signal to disable payments
      if (data && data.paymentsEnabled === false) {
        throw new Error('Payments are temporarily unavailable.');
      }

      const publishableKey = data.data?.publishableKey || data.publishableKey;
      
      if (!publishableKey) {
        throw new Error('No publishable key received from server');
      }
      
      // Clean up key (remove any whitespace/newlines)
      const cleanKey = publishableKey.trim();
      
      // Validate the key format (Stripe publishable keys start with pk_)
      if (!cleanKey.startsWith('pk_')) {
        throw new Error('Invalid Stripe publishable key format');
      }
      
      stripePromise = loadStripe(cleanKey);
      
    } catch (error) {
      console.error('CRITICAL: Failed to load Stripe configuration:', error);
      // SECURITY: Do not fall back to hardcoded keys - throw error instead
      // This ensures payment processing fails securely if configuration is missing
      throw new Error(
        'Payment system configuration error. Please contact support if this issue persists. ' +
        'Error details: ' + error.message
      );
    }
  }
  return stripePromise;
};

// Card element styling
const CARD_ELEMENT_OPTIONS = {
  style: {
    base: {
      color: '#32325d',
      fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
      fontSmoothing: 'antialiased',
      fontSize: '16px',
      '::placeholder': {
        color: '#aab7c4'
      }
    },
    invalid: {
      color: '#fa755a',
      iconColor: '#fa755a'
    }
  },
  hidePostalCode: true // We already have address info
};

// Payment form component (used inside Elements provider)
function CheckoutForm({ bookingData, displayTotal, onSuccess, onError }) {
  const stripe = useStripe();
  const elements = useElements();
  const [processing, setProcessing] = useState(false);
  const [error, setError] = useState(null);
  const [succeeded, setSucceeded] = useState(false);

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (!stripe || !elements) {
      return;
    }

    setProcessing(true);
    setError(null);

    try {
      // Step 1: Create booking in backend
      const bookingResult = await bookingAPI.createBooking(bookingData);
      
      if (!bookingResult.success) {
        throw new Error(bookingResult.message || 'Failed to create booking');
      }

      // Step 2: Create payment intent (server calculates amount)
      const paymentData = await bookingAPI.createPaymentIntent(bookingResult.data.bookingRef);
      
      if (!paymentData.success) {
        throw new Error(paymentData.message || 'Failed to create payment');
      }

      // Step 3: Confirm payment with Stripe
      const result = await stripe.confirmCardPayment(paymentData.clientSecret, {
        payment_method: {
          card: elements.getElement(CardElement),
          billing_details: {
            name: bookingData.clientName,
            email: bookingData.email,
            phone: bookingData.phone || undefined
          }
        }
      });

      if (result.error) {
        throw new Error(result.error.message);
      }

      // Step 4: Confirm payment in backend with slot revalidation
      const confirmResult = await bookingAPI.confirmPayment(
        bookingResult.data.bookingRef,
        result.paymentIntent.id
      );
      
      if (!confirmResult.success) {
        // Handle specific slot conflict errors with enhanced messaging
        let errorMessage = confirmResult.message || 'Payment processed but booking confirmation failed.';
        
        if (confirmResult.error === 'SLOT_UNAVAILABLE') {
          errorMessage = `SLOT_UNAVAILABLE: ${confirmResult.message} Your payment was successful and an automatic refund has been initiated. Please try booking a different time slot.`;
        } else if (confirmResult.error === 'DAILY_LIMIT_EXCEEDED') {
          errorMessage = `DAILY_LIMIT_EXCEEDED: ${confirmResult.message} Your payment was successful and an automatic refund has been initiated. Please try booking a different date.`;
        } else if (confirmResult.error === 'CALENDAR_CONFLICT') {
          errorMessage = `CALENDAR_CONFLICT: ${confirmResult.message} Your payment was successful and an automatic refund has been initiated. Please try booking a different time.`;
        } else {
          errorMessage = `GENERIC_ERROR: ${confirmResult.message || 'Payment processed but booking confirmation failed. Please contact support.'}`;
        }
        
        throw new Error(errorMessage);
      }

      // Success!
      setSucceeded(true);
      setProcessing(false);
      onSuccess(bookingResult.data.bookingRef);

    } catch (err) { 
      setError(err.message);
      setProcessing(false);
      onError(err.message);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/* Security badges */}
      <div className="flex items-center justify-center gap-4 text-sm text-gray-600 mb-4">
        <span className="flex items-center gap-1">
          <Lock className="w-4 h-4" />
          Secure payment
        </span>
        <span className="flex items-center gap-1">
          <CreditCard className="w-4 h-4" />
          Powered by Stripe
        </span>
      </div>

      {/* Card input */}
      <div className="border border-gray-300 rounded-lg p-4 focus-within:border-blue-500 focus-within:ring-2 focus-within:ring-blue-500 focus-within:ring-opacity-20">
        <CardElement options={CARD_ELEMENT_OPTIONS} />
      </div>

      {/* Test card info for development */}
      <div className="bg-blue-50 border border-blue-200 rounded-lg p-3">
        <p className="text-sm text-blue-900">
          <AlertCircle className="w-4 h-4 inline mr-1" />
          Test mode: Use card number <span className="font-mono font-semibold">4242 4242 4242 4242</span>
        </p>
        <p className="text-xs text-blue-700 mt-1">
          Any future expiry date and any 3-digit CVC
        </p>
      </div>

      {/* Error message */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-3">
          <p className="text-sm text-red-900">
            <AlertCircle className="w-4 h-4 inline mr-1" />
            {error}
          </p>
        </div>
      )}

      {/* Success message */}
      {succeeded && (
        <div className="bg-green-50 border border-green-200 rounded-lg p-3">
          <p className="text-sm text-green-900">
            <CheckCircle className="w-4 h-4 inline mr-1" />
            Payment successful! Redirecting...
          </p>
        </div>
      )}

      {/* Submit button */}
      <button
        type="submit"
        disabled={!stripe || processing || succeeded}
        className={`w-full py-4 rounded-lg font-semibold transition-colors ${
          processing || succeeded
            ? 'bg-gray-400 cursor-not-allowed'
            : 'bg-blue-600 hover:bg-blue-700 text-white'
        }`}
      >
        {processing ? (
          <span className="flex items-center justify-center">
            <svg className="animate-spin h-5 w-5 mr-2" viewBox="0 0 24 24">
              <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" fill="none" />
              <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
            </svg>
            Processing...
          </span>
        ) : succeeded ? (
          'Payment Complete!'
        ) : (
          `Pay $${displayTotal || 'Loading...'}`
        )}
      </button>

      {/* Additional info */}
      <p className="text-xs text-center text-gray-500">
        Your payment information is encrypted and secure. 
        We never store your card details.
      </p>
    </form>
  );
}

// Main payment component with Stripe Elements provider
export default function PaymentForm({ bookingData, displayTotal, onSuccess, onError }) {
  const [stripe, setStripe] = React.useState(null);
  const [initError, setInitError] = React.useState(null);
  const [loading, setLoading] = React.useState(true);
  
  React.useEffect(() => {
    initializeStripe()
      .then(stripeInstance => {
        setStripe(stripeInstance);
        setLoading(false);
      })
      .catch(error => {
        console.error('Stripe initialization failed:', error);
        setInitError(error.message);
        setLoading(false);
        // Also call the parent error handler to inform them of the failure
        if (onError) {
          onError('Payment system unavailable: ' + error.message);
        }
      });
  }, [onError]);
  
  if (loading) {
    return (
      <div className="text-center py-8">
        <div className="animate-spin h-8 w-8 border-b-2 border-blue-500 mx-auto"></div>
        <p className="mt-2 text-gray-600">Loading secure payment system...</p>
      </div>
    );
  }
  
  if (initError) {
    return (
      <div className="bg-red-50 border border-red-200 rounded-lg p-6 text-center">
        <AlertCircle className="mx-auto h-12 w-12 text-red-500 mb-4" />
        <h3 className="text-lg font-semibold text-red-800 mb-2">
          Payment System Unavailable
        </h3>
        <p className="text-red-700 mb-4">
          We're unable to process payments right now. This could be due to a temporary service issue.
        </p>
        <p className="text-red-600 text-sm mb-4">
          <strong>Error:</strong> {initError}
        </p>
        <div className="bg-red-100 border border-red-300 rounded p-3 text-left text-sm text-red-800">
          <p className="font-semibold mb-1">What you can do:</p>
          <ul className="list-disc list-inside space-y-1">
            <li>Refresh the page and try again</li>
            <li>Wait a few minutes and retry</li>
            <li>Contact support if the problem persists</li>
          </ul>
        </div>
      </div>
    );
  }
  
  if (!stripe) {
    return (
      <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-6 text-center">
        <AlertCircle className="mx-auto h-12 w-12 text-yellow-500 mb-4" />
        <h3 className="text-lg font-semibold text-yellow-800 mb-2">
          Payment System Loading
        </h3>
        <p className="text-yellow-700">
          Please wait while we set up the secure payment system...
        </p>
      </div>
    );
  }
  
  return (
    <Elements stripe={stripe}>
      <CheckoutForm 
        bookingData={bookingData}
        displayTotal={displayTotal}
        onSuccess={onSuccess}
        onError={onError}
      />
    </Elements>
  );
}

// Also export the stripe initialization function for use elsewhere if needed
export { initializeStripe };
