// BookingSuccess.js - Save this in frontend/src/BookingSuccess.js
import React, { useState, useEffect } from 'react';
import { CheckCircle, Calendar, MapPin, Video, Clock, Mail, Phone, Download, Home } from 'lucide-react';
import { bookingAPI } from './services/api';

function BookingSuccess() {
  const [booking, setBooking] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  useEffect(() => {
    // Get booking reference from URL
    const params = new URLSearchParams(window.location.search);
    const ref = params.get('ref');
    
    if (!ref) {
      setError('No booking reference provided');
      setLoading(false);
      return;
    }
    
    // Fetch booking details from backend
    bookingAPI.getBooking(ref)
      .then(data => {
        if (data.success) {
          setBooking(data.data);
        } else {
          setError(data.message || 'Booking not found');
        }
        setLoading(false);
      })
      .catch(() => {
        setError('Failed to load booking details');
        setLoading(false);
      });
  }, []);
  
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-AU', {
      weekday: 'long',
      day: 'numeric',
      month: 'long',
      year: 'numeric'
    });
  };
  
  const formatTime = (timeString) => {
    // Convert 24hr to 12hr format
    const [hours, minutes] = timeString.split(':');
    const hour = parseInt(hours);
    const ampm = hour >= 12 ? 'PM' : 'AM';
    const hour12 = hour > 12 ? hour - 12 : (hour === 0 ? 12 : hour);
    return `${hour12}:${minutes} ${ampm}`;
  };
  
  const addToCalendar = () => {
    if (!booking) return;
    
    try {
      // Debug: Log the actual booking data to understand the format
      console.log('🔍 Debugging booking data:', {
        booking_date: booking.booking_date,
        booking_time: booking.booking_time,
        duration: booking.duration,
        full_booking: booking
      });
      
      let startDate;
      
      // More robust date parsing
      if (booking.booking_date && booking.booking_time) {
        // Convert booking_date to a proper Date object first
        let baseDate;
        
        if (typeof booking.booking_date === 'string') {
          // Handle various string formats
          if (booking.booking_date.includes('-')) {
            // ISO format: YYYY-MM-DD
            baseDate = new Date(booking.booking_date + 'T00:00:00');
          } else if (booking.booking_date.includes('/')) {
            // US format: MM/DD/YYYY or DD/MM/YYYY
            baseDate = new Date(booking.booking_date);
          } else {
            // Try direct parsing
            baseDate = new Date(booking.booking_date);
          }
        } else if (booking.booking_date instanceof Date) {
          baseDate = booking.booking_date;
        } else {
          // Try to create from whatever format it is
          baseDate = new Date(booking.booking_date);
        }
        
        console.log('📅 Base date created:', baseDate);
        
        // Validate base date
        if (isNaN(baseDate.getTime())) {
          throw new Error(`Invalid base date: ${booking.booking_date}`);
        }
        
        // Parse time and add to base date
        let timeStr = booking.booking_time.toString();
        
        // Handle time formats: "10:00", "10:00:00", etc.
        const timeParts = timeStr.split(':');
        const hours = parseInt(timeParts[0], 10);
        const minutes = parseInt(timeParts[1] || '0', 10);
        
        console.log('🕐 Time parsed:', { hours, minutes, original: timeStr });
        
        // Validate time parts
        if (isNaN(hours) || isNaN(minutes) || hours < 0 || hours > 23 || minutes < 0 || minutes > 59) {
          throw new Error(`Invalid time format: ${booking.booking_time}`);
        }
        
        // Create the start date/time
        startDate = new Date(baseDate);
        startDate.setHours(hours, minutes, 0, 0);
        
        console.log('🎯 Final start date:', startDate);
        
      } else {
        throw new Error(`Missing booking date (${booking.booking_date}) or time (${booking.booking_time})`);
      }
      
      // Final validation of the created date
      if (isNaN(startDate.getTime())) {
        throw new Error('Invalid final date created');
      }
      
      // Calculate end time (duration is in minutes)
      const durationMinutes = parseInt(booking.duration) || 60;
      const durationMs = durationMinutes * 60 * 1000;
      const endDate = new Date(startDate.getTime() + durationMs);
      
      console.log('⏱️ Duration and end date:', { durationMinutes, endDate });
      
      // Format dates for Google Calendar (YYYYMMDDTHHMMSSZ format)
      const formatGoogleDate = (date) => {
        return date.toISOString().replace(/-|:|\.\d\d\d/g, '');
      };
      
      const event = {
        title: `Veterinary Behaviour Consultation - ${booking.pet_name}`,
        start: formatGoogleDate(startDate),
        end: formatGoogleDate(endDate),
        description: `Booking Reference: ${booking.booking_ref}\\nPet: ${booking.pet_name} (${booking.pet_breed})\\nConcerns: ${booking.concerns}`
      };
      
      console.log('📅 Calendar event created:', event);
      
      // Create Google Calendar URL
      const googleUrl = `https://calendar.google.com/calendar/render?action=TEMPLATE&text=${encodeURIComponent(event.title)}&dates=${event.start}/${event.end}&details=${encodeURIComponent(event.description)}`;
      
      console.log('🔗 Google Calendar URL:', googleUrl);
      
      window.open(googleUrl, '_blank');
    } catch (error) {
      console.error('❌ Error creating calendar event:', error);
      console.log('📊 Full booking data:', booking);
      alert('Unable to create calendar event. Please add the appointment manually to your calendar.');
    }
  };
  
  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 to-white flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading booking details...</p>
        </div>
      </div>
    );
  }
  
  if (error) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 to-white flex items-center justify-center">
        <div className="text-center">
          <div className="text-red-500 text-6xl mb-4">⚠️</div>
          <h1 className="text-2xl font-bold text-gray-900 mb-2">Booking Not Found</h1>
          <p className="text-gray-600">{error}</p>
          <a href="/" className="mt-4 inline-block px-6 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600">
            Return to Booking
          </a>
        </div>
      </div>
    );
  }
  
  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-white py-8 px-4">
      <div className="max-w-3xl mx-auto">
        {/* Success Header */}
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-20 h-20 bg-green-100 rounded-full mb-4">
            <CheckCircle className="w-12 h-12 text-green-600" />
          </div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Booking Confirmed!</h1>
          <p className="text-lg text-gray-600">Your consultation has been successfully booked</p>
        </div>
        
        {/* Booking Reference */}
        <div className="bg-blue-600 text-white rounded-xl p-6 mb-6">
          <div className="text-center">
            <p className="text-blue-100 mb-2">Booking Reference</p>
            <p className="text-3xl font-bold tracking-wider">{booking?.booking_ref}</p>
            <p className="text-sm text-blue-100 mt-2">Please save this reference for your records</p>
          </div>
        </div>
        
        {/* Booking Details */}
        <div className="bg-white rounded-xl shadow-lg p-6 mb-6">
          <h2 className="text-xl font-bold text-gray-900 mb-4">Appointment Details</h2>
          
          <div className="space-y-4">
            <div className="flex items-start gap-3">
              <Calendar className="w-5 h-5 text-gray-400 mt-0.5" />
              <div>
                <p className="font-semibold text-gray-900">{formatDate(booking?.booking_date)}</p>
                <p className="text-gray-600">{formatTime(booking?.booking_time || '')}</p>
              </div>
            </div>
            
            <div className="flex items-start gap-3">
              <Clock className="w-5 h-5 text-gray-400 mt-0.5" />
              <div>
                <p className="font-semibold text-gray-900">Duration</p>
                <p className="text-gray-600">{booking?.duration} minutes</p>
              </div>
            </div>
            
            <div className="flex items-start gap-3">
              {booking?.delivery_method === 'zoom' ? (
                <Video className="w-5 h-5 text-gray-400 mt-0.5" />
              ) : (
                <MapPin className="w-5 h-5 text-gray-400 mt-0.5" />
              )}
              <div>
                <p className="font-semibold text-gray-900">
                  {booking?.delivery_method === 'zoom' ? 'Zoom Consultation' : 'Home Visit'}
                </p>
                {booking?.delivery_method === 'home' && (
                  <p className="text-gray-600">{booking?.suburb}, {booking?.postcode}</p>
                )}
              </div>
            </div>
            
            <div className="flex items-start gap-3">
              <div className="w-5 h-5 text-gray-400 mt-0.5">🐾</div>
              <div>
                <p className="font-semibold text-gray-900">Pet Details</p>
                <p className="text-gray-600">{booking?.pet_name} - {booking?.pet_breed}</p>
              </div>
            </div>
          </div>
          
          <button
            onClick={addToCalendar}
            className="mt-6 w-full py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors flex items-center justify-center gap-2"
          >
            <Calendar className="w-5 h-5" />
            Add to Google Calendar
          </button>
        </div>
        
        {/* Next Steps */}
        <div className="bg-white rounded-xl shadow-lg p-6 mb-6">
          <h2 className="text-xl font-bold text-gray-900 mb-4">What Happens Next?</h2>
          
          <div className="space-y-4">
            <div className="flex gap-3">
              <div className="flex-shrink-0 w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                <span className="text-sm font-bold text-blue-600">1</span>
              </div>
              <div>
                <p className="font-semibold text-gray-900">Confirmation Email</p>
                <p className="text-gray-600">
                  You'll receive a confirmation email at {booking?.client_email} with all appointment details
                  {booking?.delivery_method === 'zoom' && ' and your Zoom meeting link'}.
                </p>
              </div>
            </div>
            
            <div className="flex gap-3">
              <div className="flex-shrink-0 w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                <span className="text-sm font-bold text-blue-600">2</span>
              </div>
              <div>
                <p className="font-semibold text-gray-900">Prepare for Your Consultation</p>
                <p className="text-gray-600">
                  Make notes about your pet's behaviour patterns, triggers, and any specific incidents you'd like to discuss.
                </p>
              </div>
            </div>
            
            <div className="flex gap-3">
              <div className="flex-shrink-0 w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                <span className="text-sm font-bold text-blue-600">3</span>
              </div>
              <div>
                <p className="font-semibold text-gray-900">Reminder</p>
                <p className="text-gray-600">
                  We'll send you a reminder 48 hours and 3 hours before your appointment.
                </p>
              </div>
            </div>
            
            {booking?.delivery_method === 'zoom' && (
              <div className="flex gap-3">
                <div className="flex-shrink-0 w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                  <span className="text-sm font-bold text-blue-600">4</span>
                </div>
                <div>
                  <p className="font-semibold text-gray-900">Join Your Zoom Session</p>
                  <p className="text-gray-600">
                    On the day, click the Zoom link in your email to join. Please test your camera and microphone beforehand.
                  </p>
                </div>
              </div>
            )}
            
            {booking?.delivery_method === 'home' && (
              <div className="flex gap-3">
                <div className="flex-shrink-0 w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
                  <span className="text-sm font-bold text-blue-600">4</span>
                </div>
                <div>
                  <p className="font-semibold text-gray-900">Home Visit Preparation</p>
                  <p className="text-gray-600">
                    Please ensure your pet is at home and you have a quiet space available for the consultation.
                  </p>
                </div>
              </div>
            )}
          </div>
        </div>
        
        {/* Contact Information */}
        <div className="bg-gray-50 rounded-xl p-6 mb-6">
          <h3 className="font-semibold text-gray-900 mb-3">Need to Make Changes?</h3>
          <p className="text-gray-600 mb-4">
            If you need to reschedule or cancel your appointment, please contact us at least 24 hours in advance.
          </p>
          <div className="space-y-2">
            <a href="mailto:info@petbehaviourservices.com.au" className="flex items-center gap-2 text-blue-600 hover:text-blue-700">
              <Mail className="w-4 h-4" />
              info@petbehaviourservices.com.au
            </a>
          </div>
        </div>
        
        {/* Action Buttons */}
        <div className="flex gap-4">
          <button
            onClick={() => window.print()}
            className="flex-1 py-3 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors flex items-center justify-center gap-2"
          >
            <Download className="w-5 h-5" />
            Print Details
          </button>
          <a
            href="/"
            className="flex-1 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors flex items-center justify-center gap-2"
          >
            <Home className="w-5 h-5" />
            Book Another
          </a>
        </div>
      </div>
    </div>
  );
}

export default BookingSuccess;