// Hybrid API configuration - uses Vercel for payments, production for everything else
// This fixes the Stripe client secret format issue

const API_CONFIG = {
  // Main API for booking operations
  BOOKING_API: 'https://api.petbehaviourservices.com.au/api',
  
  // Vercel API for payment processing (properly formatted client secrets)
  PAYMENT_API: 'https://vercel-payment-system-owpjqf6ky-gleggs-projects.vercel.app/api',
  
  // Feature flags
  USE_HYBRID_SETUP: true,
  USE_VERCEL_PAYMENTS: true,
  
  // Timeouts
  API_TIMEOUT: 10000,
  PAYMENT_TIMEOUT: 30000
};

// Enhanced API routing
export const getApiUrl = (endpoint) => {
  // Payment-related endpoints go to Vercel
  const paymentEndpoints = [
    '/stripe-key',
    '/stripe-config', 
    '/create-payment-intent',
    '/reserve-slot',
    '/cleanup-reservations'
  ];
  
  if (paymentEndpoints.some(pe => endpoint.includes(pe))) {
    return API_CONFIG.PAYMENT_API + endpoint;
  }
  
  // All other endpoints go to main API
  return API_CONFIG.BOOKING_API + endpoint;
};

// Enhanced fetch with automatic routing
export const apiRequest = async (endpoint, options = {}) => {
  const url = getApiUrl(endpoint);
  const timeout = endpoint.includes('payment') || endpoint.includes('stripe') 
    ? API_CONFIG.PAYMENT_TIMEOUT 
    : API_CONFIG.API_TIMEOUT;

  console.log(`🌐 API Request: ${options.method || 'GET'} ${url}`);
  
  const controller = new AbortController();
  const timeoutId = setTimeout(() => controller.abort(), timeout);
  
  try {
    const response = await fetch(url, {
      ...options,
      signal: controller.signal,
      headers: {
        'Content-Type': 'application/json',
        ...options.headers
      }
    });
    
    clearTimeout(timeoutId);
    
    if (!response.ok) {
      throw new Error(`HTTP ${response.status}: ${response.statusText}`);
    }
    
    const data = await response.json();
    console.log(`✅ API Success: ${url}`);
    return data;
    
  } catch (error) {
    clearTimeout(timeoutId);
    console.error(`❌ API Error: ${url} - ${error.message}`);
    throw error;
  }
};

// Hybrid booking API with smart routing
export const hybridBookingAPI = {
  // Main API endpoints (production server)
  getServices: () => apiRequest('/services'),
  checkPostcode: (postcode) => apiRequest('/check-postcode', {
    method: 'POST',
    body: JSON.stringify({ postcode })
  }),
  getPricingPreview: (serviceId, deliveryMethod, postcode) => 
    apiRequest('/pricing-preview', {
      method: 'POST',
      body: JSON.stringify({ serviceId, deliveryMethod, postcode })
    }),
  getAvailableDates: (month, year, deliveryMethod, postcode, serviceId) =>
    apiRequest('/get-available-dates', {
      method: 'POST',
      body: JSON.stringify({ month, year, deliveryMethod, postcode, serviceId })
    }),
  getAvailableSlots: (date, deliveryMethod, serviceId) =>
    apiRequest('/get-available-slots', {
      method: 'POST',
      body: JSON.stringify({ date, deliveryMethod, serviceId })
    }),

  // Payment API endpoints (Vercel - properly formatted client secrets)
  getStripeConfig: () => apiRequest('/stripe-config'),
  createPaymentIntent: (bookingData) =>
    apiRequest('/create-payment-intent', {
      method: 'POST',
      body: JSON.stringify(bookingData)
    }),
  reserveSlot: (date, time, sessionId, serviceId) => 
    apiRequest('/reserve-slot', {
      method: 'POST',
      body: JSON.stringify({ date, time, sessionId, serviceId })
    }),
  
  // Legacy create booking (main API)  
  createBooking: (bookingData) => apiRequest('/create-booking', {
    method: 'POST',
    body: JSON.stringify(bookingData)
  })
};

// Export configuration
export default {
  ...API_CONFIG,
  getApiUrl,
  apiRequest,
  hybridBookingAPI
};

console.log('🔧 Hybrid API Configuration Loaded');
console.log('📡 Main API:', API_CONFIG.BOOKING_API);
console.log('💳 Payment API:', API_CONFIG.PAYMENT_API);
console.log('✅ Using Vercel for payment processing with proper Stripe client secrets');