// Centralized API configuration
// This file provides environment-aware API endpoints

// Determine base URL with fallbacks:
// 1. Window global set by host page (e.g., WordPress)
// 2. Environment variable at build time
// 3. Default production API URL
const DEFAULT_API_BASE = 'https://api.petbehaviourservices.com.au/api';

const API_BASE_URL =
  (typeof window !== 'undefined' && window.__BOOKING_API_BASE) ||
  process.env.REACT_APP_API_URL ||
  DEFAULT_API_BASE;

// Ensure the URL doesn't have trailing slash
const normalizedBaseUrl = API_BASE_URL.replace(/\/$/, '');

export const API_CONFIG = {
  BASE_URL: normalizedBaseUrl,
  
  // Specific endpoints
  ENDPOINTS: {
    STRIPE_CONFIG: '/stripe-config',
    CREATE_BOOKING: '/create-booking',
    CREATE_PAYMENT_INTENT: '/create-payment-intent',
    CONFIRM_PAYMENT: '/confirm-payment',
    GET_BOOKING: (bookingRef) => `/booking/${bookingRef}`,
    ADMIN_LOGIN: '/admin/login',
    ADMIN_BOOKINGS: '/admin/bookings',
    ADMIN_STATS: '/admin/stats',
    ADMIN_BOOKING_STATUS: (id) => `/admin/bookings/${id}/status`,
    ADMIN_DELETE_BOOKING: (id) => `/admin/bookings/${id}`,
    ADMIN_EXPORT: '/admin/bookings/export'
  },
  
  // Helper function to build full URLs
  getUrl: (endpoint) => `${normalizedBaseUrl}${endpoint}`,
  
  // Helper function for admin endpoints
  getAdminUrl: (endpoint) => `${normalizedBaseUrl}/admin${endpoint}`,
  
  // Get base URL without /api suffix (for other services)
  getBaseUrlWithoutApi: () => normalizedBaseUrl.replace('/api', '')
};

// Log configuration in development
if (process.env.NODE_ENV === 'development') {
  console.log('🔧 API Configuration UPDATED:', {
    baseUrl: API_CONFIG.BASE_URL,
    environment: process.env.NODE_ENV,
    source: process.env.REACT_APP_API_URL ? 'env var' : 'default',
    timestamp: new Date().toISOString()
  });
}

export default API_CONFIG;
