// API service for veterinary booking system
import axios from 'axios';

import API_CONFIG from '../config/api.js';

const API_BASE_URL = API_CONFIG.BASE_URL;


// Create axios instance with default config
const api = axios.create({
  baseURL: API_BASE_URL,
  headers: {
    'Content-Type': 'application/json',
  },
  withCredentials: true
});

// Add response interceptor for error handling
api.interceptors.response.use(
  (response) => response,
  (error) => {
    console.error('API Error:', error.response?.data || error.message);
    return Promise.reject(error);
  }
);

export const bookingAPI = {
  // Get available services
  getServices: async () => {
    try {
      const response = await api.get('/services');
      if (typeof window !== 'undefined') {
        try {
          localStorage.setItem('cachedServices', JSON.stringify(response.data));
        } catch (e) {
          console.warn('Failed to cache services', e);
        }
      }
      return response.data;
    } catch (error) {
      const isCorsOrNetwork = !error.response;
      const customError = new Error('Failed to fetch services');
      customError.isNetwork = isCorsOrNetwork;
      throw customError;
    }
  },

  // Check postcode and get travel info
  checkPostcode: async (postcode) => {
    try {
      const response = await api.post('/check-postcode', { postcode });
      return response.data;
    } catch (error) {
      if (error.response?.status === 404) {
        return {
          success: false,
          message: 'Service not available in this area. Please consider a Zoom consultation.'
        };
      }
      throw new Error('Failed to validate postcode');
    }
  },

  // Get available dates for booking
  getAvailableDates: async (deliveryMethod, month, year, postcode = null, serviceId = null) => {
    try {
      const requestData = {
        deliveryMethod,
        month,
        year
      };
      
      // Include postcode for home visits to enable region-specific scheduling
      if (deliveryMethod === 'home' && postcode) {
        requestData.postcode = postcode;
      }
      
      // Include serviceId for time slot availability checking
      if (serviceId) {
        requestData.serviceId = serviceId;
      }
      
      const response = await api.post('/get-available-dates', requestData);
      return response.data;
    } catch (error) {
      throw new Error('Failed to fetch available dates');
    }
  },

  // Get available time slots for a date
  getAvailableSlots: async (params) => {
    try {
      const response = await api.post('/get-available-slots', params);
      return response.data;
    } catch (error) {
      throw new Error('Failed to fetch available time slots');
    }
  },

  // Create a new booking
  createBooking: async (bookingData) => {
    try {
      const response = await api.post('/create-booking', bookingData);
      return response.data;
    } catch (error) {
      throw new Error(error.response?.data?.message || 'Failed to create booking');
    }
  },

  // Create payment intent
  createPaymentIntent: async (bookingRef, amount) => {
    try {
      const response = await api.post('/create-payment-intent', {
        bookingRef,
        amount
      });
      return response.data;
    } catch (error) {
      throw new Error('Failed to create payment intent');
    }
  },

  // Confirm payment
  confirmPayment: async (bookingRef, paymentIntentId) => {
    try {
      const response = await api.post('/confirm-payment', {
        bookingRef,
        paymentIntentId
      });
      return response.data;
    } catch (error) {
      throw new Error('Failed to confirm payment');
    }
  },

  // Get booking details
  getBooking: async (bookingRef) => {
    try {
      const response = await api.get(`/booking/${bookingRef}`);
      return response.data;
    } catch (error) {
      throw new Error('Failed to fetch booking details');
    }
  },

  // Get Stripe publishable key
  getStripeKey: async () => {
    try {
      const response = await api.get('/stripe-key');
      return response.data;
    } catch (error) {
      throw new Error('Failed to fetch Stripe key');
    }
  },

  // Get pricing preview for display (not used for billing)
  getPricingPreview: async (params) => {
    try {
      const response = await api.post('/pricing-preview', params);
      return response.data;
    } catch (error) {
      throw new Error('Failed to fetch pricing preview');
    }
  },

  // Get booking configuration settings
  getBookingConfig: async () => {
    try {
      const response = await api.get('/booking-config');
      return response.data;
    } catch (error) {
      throw new Error('Failed to fetch booking configuration');
    }
  }
};

export default bookingAPI;