// Frontend timezone utility for Australia/Melbourne business operations
// Ensures consistent date handling between client and server

const BUSINESS_TIMEZONE = 'Australia/Melbourne';

/**
 * Get current date/time in Australia/Melbourne timezone
 * @param {Date} [date] - Optional date to convert, defaults to current time
 * @returns {Date} Date object adjusted to Melbourne timezone
 */
export function getMelbourneTime(date = null) {
  const baseDate = date || new Date();
  
  // Use Intl.DateTimeFormat to get accurate timezone-aware date
  const formatter = new Intl.DateTimeFormat('en-AU', {
    timeZone: BUSINESS_TIMEZONE,
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
    hour12: false
  });
  
  const parts = formatter.formatToParts(baseDate);
  const partsObj = {};
  parts.forEach(part => {
    partsObj[part.type] = part.value;
  });
  
  // Create new date object representing Melbourne time
  const melbourneDate = new Date(
    parseInt(partsObj.year),
    parseInt(partsObj.month) - 1, // JavaScript months are 0-based
    parseInt(partsObj.day),
    parseInt(partsObj.hour),
    parseInt(partsObj.minute),
    parseInt(partsObj.second)
  );
  
  return melbourneDate;
}

/**
 * Get start of day (00:00:00) in Australia/Melbourne timezone
 * @param {Date|string} date - Date to get start of day for
 * @returns {Date} Start of day in Melbourne timezone
 */
export function getMelbourneStartOfDay(date) {
  const inputDate = typeof date === 'string' ? new Date(date) : date;
  
  // Get the date components in Melbourne timezone
  const formatter = new Intl.DateTimeFormat('en-AU', {
    timeZone: BUSINESS_TIMEZONE,
    year: 'numeric',
    month: '2-digit',
    day: '2-digit'
  });
  
  const parts = formatter.formatToParts(inputDate);
  const partsObj = {};
  parts.forEach(part => {
    partsObj[part.type] = part.value;
  });
  
  // Create start of day in Melbourne timezone
  const startOfDay = new Date(
    parseInt(partsObj.year),
    parseInt(partsObj.month) - 1,
    parseInt(partsObj.day),
    0, 0, 0, 0
  );
  
  return startOfDay;
}

/**
 * Get day of week for a date in Australia/Melbourne timezone
 * @param {Date|string} date - Date to get day of week for
 * @returns {number} Day of week (0=Sunday, 1=Monday, ..., 6=Saturday)
 */
export function getMelbourneDayOfWeek(date) {
  const inputDate = typeof date === 'string' ? new Date(date) : date;
  
  const formatter = new Intl.DateTimeFormat('en-AU', {
    timeZone: BUSINESS_TIMEZONE,
    weekday: 'long'
  });
  
  const weekdayName = formatter.format(inputDate);
  const weekdays = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
  return weekdays.indexOf(weekdayName);
}

/**
 * Check if a date is a working day in Australia/Melbourne timezone
 * @param {Date|string} date - Date to check
 * @param {Array} workingDays - Array of working day names (optional, defaults to backend config)
 * @returns {boolean} True if it's a working day
 */
export function isMelbourneWorkingDay(date, workingDays = ['monday', 'tuesday', 'thursday', 'friday']) {
  const dayOfWeek = getMelbourneDayOfWeek(date);
  const dayNames = ['sunday', 'monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday'];
  const currentDayName = dayNames[dayOfWeek];
  return workingDays.includes(currentDayName);
}

/**
 * Get current business date in Australia/Melbourne timezone
 * @returns {string} Date string in YYYY-MM-DD format
 */
export function getMelbourneBusinessDate() {
  const melbourneNow = getMelbourneTime();
  return melbourneNow.toISOString().split('T')[0];
}

/**
 * Add days to a date in Australia/Melbourne timezone
 * @param {Date|string} date - Starting date
 * @param {number} days - Number of days to add
 * @returns {Date} New date with days added, adjusted for Melbourne timezone
 */
export function addDaysInMelbourne(date, days) {
  const inputDate = typeof date === 'string' ? new Date(date) : new Date(date);
  const melbourneDate = getMelbourneTime(inputDate);
  melbourneDate.setDate(melbourneDate.getDate() + days);
  return melbourneDate;
}

/**
 * Calculate difference in days between two dates in Melbourne timezone
 * @param {Date|string} date1 - First date
 * @param {Date|string} date2 - Second date  
 * @returns {number} Difference in days (date2 - date1)
 */
export function daysDifferenceInMelbourne(date1, date2) {
  const melbDate1 = getMelbourneStartOfDay(date1);
  const melbDate2 = getMelbourneStartOfDay(date2);
  
  const diffTime = melbDate2.getTime() - melbDate1.getTime();
  const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
  return diffDays;
}

/**
 * Format a date for display in Australia/Melbourne timezone
 * @param {Date|string} date - Date to format
 * @param {object} options - Intl.DateTimeFormat options
 * @returns {string} Formatted date string
 */
export function formatMelbourneDate(date, options = {}) {
  const inputDate = typeof date === 'string' ? new Date(date) : date;
  
  const defaultOptions = {
    timeZone: BUSINESS_TIMEZONE,
    year: 'numeric',
    month: 'long',
    day: 'numeric',
    ...options
  };
  
  return new Intl.DateTimeFormat('en-AU', defaultOptions).format(inputDate);
}

/**
 * Check if we're currently in daylight saving time in Melbourne
 * @param {Date} [date] - Date to check, defaults to current time
 * @returns {boolean} True if DST is active
 */
export function isMelbourneDST(date = new Date()) {
  // Create two dates in Melbourne timezone - one in known DST period (Dec) and one in standard time (Jun)
  const testDateDST = new Date(date.getFullYear(), 11, 15); // December 15
  const testDateStandard = new Date(date.getFullYear(), 5, 15); // June 15
  
  // Get timezone offsets for Melbourne
  const dstFormatter = new Intl.DateTimeFormat('en-AU', {
    timeZone: BUSINESS_TIMEZONE,
    timeZoneName: 'short'
  });
  
  const currentFormatter = new Intl.DateTimeFormat('en-AU', {
    timeZone: BUSINESS_TIMEZONE,
    timeZoneName: 'short'
  });
  
  const dstTz = dstFormatter.formatToParts(testDateDST).find(part => part.type === 'timeZoneName')?.value;
  const currentTz = currentFormatter.formatToParts(date).find(part => part.type === 'timeZoneName')?.value;
  
  // AEDT (Australian Eastern Daylight Time) indicates DST is active
  return currentTz === 'AEDT';
}

/**
 * Get available booking dates starting from configurable business days ahead
 * @param {Object} config - Booking configuration (optional)
 * @param {Array} config.working_days - Array of working day names
 * @param {number} config.booking_advance_min - Minimum days in advance
 * @param {number} config.booking_advance_max - Maximum days in advance
 * @returns {Date[]} Array of available booking dates
 */
export function getAvailableBookingDates(config = {}) {
  const {
    working_days = ['monday', 'tuesday', 'thursday', 'friday'],
    booking_advance_min = 2,
    booking_advance_max = 56
  } = config;
  
  const dates = [];
  const startDate = addDaysInMelbourne(getMelbourneTime(), booking_advance_min);
  
  for (let i = 0; i < booking_advance_max; i++) {
    const date = addDaysInMelbourne(startDate, i);
    
    // Only include working days based on dynamic config
    if (isMelbourneWorkingDay(date, working_days)) {
      dates.push(date);
    }
  }
  
  return dates;
}

/**
 * Get calendar dates for month view in Melbourne timezone
 * @param {number} month - Month (0-based)
 * @param {number} year - Year
 * @returns {Date[]} Array of dates for the month
 */
export function getMelbourneCalendarDates(month, year, config = {}) {
  const firstDay = new Date(year, month, 1);
  const lastDay = new Date(year, month + 1, 0);
  const dates = [];
  
  // Add all dates in the month, adjusted to Melbourne timezone
  for (let d = 1; d <= lastDay.getDate(); d++) {
    const date = new Date(year, month, d);
    // Ensure date is represented correctly in Melbourne timezone
    dates.push(getMelbourneStartOfDay(date));
  }
  
  return dates;
}

/**
 * Check if date navigation is allowed (within booking window)
 * @param {Date} date - Date to check
 * @param {Object} config - Booking configuration (optional)
 * @returns {boolean} True if navigation to this date is allowed
 */
export function isDateNavigationAllowed(date, config = {}) {
  const { booking_advance_max = 56 } = config;
  const today = getMelbourneTime();
  const maxDate = addDaysInMelbourne(today, booking_advance_max);
  
  return date >= getMelbourneStartOfDay(today) && date <= getMelbourneStartOfDay(maxDate);
}

// Log timezone info in development
if (process.env.NODE_ENV === 'development') {
  const now = new Date();
  const melbNow = getMelbourneTime(now);
  const isDST = isMelbourneDST(now);
  
  console.log('🕐 Frontend Timezone Configuration:', {
    browserTime: now.toISOString(),
    melbourneTime: melbNow.toISOString(),
    isDST: isDST,
    timezone: BUSINESS_TIMEZONE,
    workingDays: 'Mon, Tue, Thu, Fri'
  });
}

export default {
  BUSINESS_TIMEZONE,
  getMelbourneTime,
  getMelbourneStartOfDay,
  getMelbourneDayOfWeek,
  isMelbourneWorkingDay,
  getMelbourneBusinessDate,
  addDaysInMelbourne,
  daysDifferenceInMelbourne,
  formatMelbourneDate,
  isMelbourneDST,
  getAvailableBookingDates,
  getMelbourneCalendarDates,
  isDateNavigationAllowed
};