// wizardNavigation.js - Centralized wizard flow management
// This handles all step navigation logic to make it more maintainable

// Define the wizard steps with their properties
export const WIZARD_STEPS = {
  SERVICE_SELECTION: {
    id: 1,
    name: 'service-selection',
    title: 'Choose Your Service',
    requiredFields: ['service'],
    allowedFor: ['home', 'zoom']
  },
  DELIVERY_METHOD: {
    id: 2,
    name: 'delivery-method',
    title: 'How would you like to meet?',
    requiredFields: ['deliveryMethod'],
    allowedFor: ['home', 'zoom']
  },
  LOCATION_DETAILS: {
    id: 3,
    name: 'location-details',
    title: 'Your Location',
    requiredFields: ['postcode'],
    allowedFor: ['home'], // Only for home visits
    skipFor: ['zoom']
  },
  CLIENT_DETAILS: {
    id: 4,
    name: 'client-details',
    title: 'Your Details',
    requiredFields: ['clientName', 'email', 'petName', 'breed', 'concerns'],
    allowedFor: ['home', 'zoom']
  },
  DATETIME_SELECTION: {
    id: 5,
    name: 'datetime-selection',
    title: 'Select Date & Time',
    requiredFields: ['selectedDate', 'selectedTime'],
    allowedFor: ['home', 'zoom']
  },
  PAYMENT_SUMMARY: {
    id: 6,
    name: 'payment-summary',
    title: 'Booking Summary',
    requiredFields: [],
    allowedFor: ['home', 'zoom']
  }
};

// Get all steps as array
export const getAllSteps = () => Object.values(WIZARD_STEPS).sort((a, b) => a.id - b.id);

// Get steps that should be shown for a given delivery method
export const getActiveSteps = (deliveryMethod) => {
  if (!deliveryMethod) return getAllSteps();
  
  return getAllSteps().filter(step => 
    step.allowedFor.includes(deliveryMethod) && 
    !step.skipFor?.includes(deliveryMethod)
  );
};

// Get the next step ID
export const getNextStepId = (currentStepId, deliveryMethod) => {
  const activeSteps = getActiveSteps(deliveryMethod);
  const currentIndex = activeSteps.findIndex(step => step.id === currentStepId);
  
  if (currentIndex === -1 || currentIndex >= activeSteps.length - 1) {
    return null; // No next step
  }
  
  return activeSteps[currentIndex + 1].id;
};

// Get the previous step ID
export const getPreviousStepId = (currentStepId, deliveryMethod) => {
  const activeSteps = getActiveSteps(deliveryMethod);
  const currentIndex = activeSteps.findIndex(step => step.id === currentStepId);
  
  if (currentIndex <= 0) {
    return null; // No previous step
  }
  
  return activeSteps[currentIndex - 1].id;
};

// Get step info by ID
export const getStepById = (stepId) => {
  return getAllSteps().find(step => step.id === stepId);
};

// Check if step should be shown for delivery method
export const isStepVisible = (stepId, deliveryMethod) => {
  const step = getStepById(stepId);
  if (!step || !deliveryMethod) return false;
  
  return step.allowedFor.includes(deliveryMethod) && 
         !step.skipFor?.includes(deliveryMethod);
};

// Get display step number (accounting for skipped steps)
export const getDisplayStepNumber = (currentStepId, deliveryMethod) => {
  const activeSteps = getActiveSteps(deliveryMethod);
  const currentIndex = activeSteps.findIndex(step => step.id === currentStepId);
  
  return currentIndex + 1; // 1-based index
};

// Get total number of active steps
export const getTotalActiveSteps = (deliveryMethod) => {
  return getActiveSteps(deliveryMethod).length;
};

// Enhanced validation with specific error messages
export const validateStep = (stepId, formData, extraValidation = {}) => {
  const step = getStepById(stepId);
  if (!step) return { isValid: false, errors: { general: 'Invalid step' } };
  
  const errors = {};
  
  // Check required fields
  step.requiredFields.forEach(field => {
    if (!formData[field]) {
      errors[field] = getFieldErrorMessage(field);
    }
  });
  
  // Step-specific validation
  switch (stepId) {
    case WIZARD_STEPS.LOCATION_DETAILS.id:
      // Only validate if this step is visible
      if (isStepVisible(stepId, formData.deliveryMethod)) {
        if (!extraValidation.postcodeValidated) {
          errors.postcode = 'Please validate your postcode';
        }
      }
      break;
      
    case WIZARD_STEPS.CLIENT_DETAILS.id:
      // Enhanced phone validation
      if (formData.phone) {
        const phoneError = validatePhoneNumber(formData.phone);
        if (phoneError) {
          errors.phone = phoneError;
        }
      }
      
      // Enhanced email validation
      if (formData.email) {
        const emailError = validateEmail(formData.email);
        if (emailError) {
          errors.email = emailError;
        }
      }
      break;
  }
  
  return {
    isValid: Object.keys(errors).length === 0,
    errors
  };
};

// Get user-friendly error messages for fields
const getFieldErrorMessage = (field) => {
  const messages = {
    service: 'Please select a service',
    deliveryMethod: 'Please select a delivery method',
    postcode: 'Please enter your postcode',
    clientName: 'Please enter your full name',
    email: 'Please enter your email address',
    phone: 'Please enter your phone number',
    petName: 'Please enter your pet\'s name',
    breed: 'Please enter your pet\'s breed',
    concerns: 'Please describe your concerns or questions',
    selectedDate: 'Please select a date for your appointment',
    selectedTime: 'Please select a time for your appointment'
  };
  
  return messages[field] || `${field} is required`;
};

// Enhanced phone number validation
export const validatePhoneNumber = (phone) => {
  // Phone is optional - return null if empty
  if (!phone || typeof phone !== 'string' || phone.trim() === '') return null;
  
  // Remove spaces, dashes, parentheses
  const cleanPhone = phone.replace(/[\s\-\(\)]/g, '');
  
  // Check if it's all digits (optionally starting with +)
  const phoneRegex = /^\+?[0-9]{8,15}$/;
  
  if (!phoneRegex.test(cleanPhone)) {
    return 'Please enter a valid phone number (8-15 digits)';
  }
  
  // Australian phone number specific checks
  if (cleanPhone.startsWith('04') && cleanPhone.length !== 10) {
    return 'Australian mobile numbers must be 10 digits (04xxxxxxxx)';
  }
  
  if (cleanPhone.startsWith('0') && cleanPhone.length < 8) {
    return 'Australian phone numbers must be at least 8 digits';
  }
  
  return null; // Valid
};

// Enhanced email validation
export const validateEmail = (email) => {
  if (!email || typeof email !== 'string') return 'Email address is required';
  
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  
  if (!emailRegex.test(email)) {
    return 'Please enter a valid email address';
  }
  
  if (email.length > 100) {
    return 'Email address must be less than 100 characters';
  }
  
  return null; // Valid
};

// Navigation helper class
export class WizardNavigator {
  constructor(deliveryMethod = null) {
    this.deliveryMethod = deliveryMethod;
  }
  
  updateDeliveryMethod(newDeliveryMethod) {
    this.deliveryMethod = newDeliveryMethod;
  }
  
  canGoNext(currentStepId) {
    return getNextStepId(currentStepId, this.deliveryMethod) !== null;
  }
  
  canGoPrevious(currentStepId) {
    return getPreviousStepId(currentStepId, this.deliveryMethod) !== null;
  }
  
  getNext(currentStepId) {
    return getNextStepId(currentStepId, this.deliveryMethod);
  }
  
  getPrevious(currentStepId) {
    return getPreviousStepId(currentStepId, this.deliveryMethod);
  }
  
  getProgress(currentStepId) {
    const activeSteps = getActiveSteps(this.deliveryMethod);
    const currentIndex = activeSteps.findIndex(step => step.id === currentStepId);
    
    // Safety checks to prevent invalid percentages
    const validCurrentIndex = Math.max(0, Math.min(currentIndex, activeSteps.length - 1));
    const validTotal = Math.max(1, activeSteps.length);
    const rawPercentage = ((validCurrentIndex + 1) / validTotal) * 100;
    const safePercentage = Math.max(0, Math.min(100, Math.round(rawPercentage)));
    
    return {
      current: validCurrentIndex + 1,
      total: validTotal,
      percentage: safePercentage
    };
  }
  
  validateCurrentStep(currentStepId, formData, extraValidation = {}) {
    return validateStep(currentStepId, formData, extraValidation);
  }
}

// Export convenience functions for backward compatibility
export const wizardNavigation = {
  WIZARD_STEPS,
  getAllSteps,
  getActiveSteps,
  getNextStepId,
  getPreviousStepId,
  getStepById,
  isStepVisible,
  getDisplayStepNumber,
  getTotalActiveSteps,
  validateStep,
  validatePhoneNumber,
  validateEmail,
  WizardNavigator
};